"use client"

import { useEffect, useState, useRef, Suspense } from "react"
import { useSearchParams } from "next/navigation"
import {
  apiSources,
  chatApi,
  type ApiSource,
  type ChatSession,
  type ChatMessage,
} from "@/lib/api-client"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  MessageSquare,
  Send,
  Plus,
  Trash2,
  Globe,
  Bot,
  User,
} from "lucide-react"
import { toast } from "sonner"
import { cn } from "@/lib/utils"

function ChatPageContent() {
  const searchParams = useSearchParams()
  const initialSourceId = searchParams.get("source")

  const [sources, setSources] = useState<ApiSource[]>([])
  const [selectedSourceId, setSelectedSourceId] = useState<string>(initialSourceId || "")
  const [sessions, setSessions] = useState<ChatSession[]>([])
  const [activeSession, setActiveSession] = useState<ChatSession | null>(null)
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [input, setInput] = useState("")
  const [sending, setSending] = useState(false)

  const messagesEndRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    loadSources()
  }, [])

  useEffect(() => {
    if (selectedSourceId) loadSessions(Number(selectedSourceId))
    else {
      setSessions([])
      setActiveSession(null)
      setMessages([])
    }
  }, [selectedSourceId])

  useEffect(() => {
    if (activeSession) loadMessages(activeSession.id)
    else setMessages([])
  }, [activeSession])

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages, sending])

  const loadSources = async () => {
    try {
      const data = await apiSources.list()
      setSources(data.filter((s) => s.status === "ready"))
    } catch {
      toast.error("Failed to load sources")
    }
  }

  const loadSessions = async (sourceId: number) => {
    try {
      const data = await chatApi.sessions(sourceId)
      setSessions(data)
      setActiveSession(data[0] || null)
    } catch {
      setSessions([])
    }
  }

  const loadMessages = async (sessionId: number) => {
    try {
      const data = await chatApi.messages(sessionId)
      setMessages(data)
    } catch {
      setMessages([])
    }
  }

  const createSession = async () => {
    if (!selectedSourceId) return toast.error("Select a source first")
    const session = await chatApi.createSession(Number(selectedSourceId), "New Chat")
    setSessions((prev) => [session, ...prev])
    setActiveSession(session)
  }

  const deleteSession = async (id: number) => {
    await chatApi.deleteSession(id)
    const updated = sessions.filter((s) => s.id !== id)
    setSessions(updated)
    if (activeSession?.id === id) setActiveSession(updated[0] || null)
  }

  const sendMessage = async () => {
    if (!input.trim() || !activeSession || sending) return

    const question = input.trim()
    setInput("")
    setSending(true)

    const tempUserMsg: ChatMessage = { role: "user", content: question }
    setMessages((prev) => [...prev, tempUserMsg])

    try {
      const response = await chatApi.sendMessage(activeSession.id, question)
      setMessages((prev) => [...prev, response])
    } catch {
      setMessages((prev) => [
        ...prev,
        { role: "assistant", content: "Something went wrong." },
      ])
    } finally {
      setSending(false)
    }
  }

  const selectedSource = sources.find((s) => s.id === Number(selectedSourceId))

  return (
    <div className="flex h-[calc(100vh-140px)] gap-6">

      {/* SESSION PANEL */}
      <div className="hidden w-64 flex-col rounded-2xl border bg-background p-4 md:flex">
        <div className="mb-4 flex items-center justify-between">
          <p className="text-sm font-semibold">Sessions</p>
          <Button size="icon" variant="ghost" onClick={createSession}>
            <Plus className="h-4 w-4" />
          </Button>
        </div>

        <div className="flex flex-1 flex-col gap-1 overflow-y-auto">
          {sessions.map((session) => (
            <div
              key={session.id}
              onClick={() => setActiveSession(session)}
              className={cn(
                "group cursor-pointer rounded-xl px-3 py-2 text-sm transition",
                activeSession?.id === session.id
                  ? "bg-primary/10 text-primary"
                  : "text-muted-foreground hover:bg-muted"
              )}
            >
              <div className="flex items-center justify-between">
                <span className="truncate">{session.title}</span>
                <Trash2
                  onClick={(e) => {
                    e.stopPropagation()
                    deleteSession(session.id)
                  }}
                  className="h-3 w-3 opacity-0 group-hover:opacity-100"
                />
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* CHAT AREA */}
      <div className="flex flex-1 flex-col rounded-2xl border bg-background shadow-sm">

        {/* HEADER */}
        <div className="flex items-center justify-between border-b px-6 py-4">
          <div className="flex items-center gap-3">
            <Select value={selectedSourceId} onValueChange={setSelectedSourceId}>
              <SelectTrigger className="w-64">
                <SelectValue placeholder="Select source" />
              </SelectTrigger>
              <SelectContent>
                {sources.map((source) => (
                  <SelectItem key={source.id} value={String(source.id)}>
                    <div className="flex items-center gap-2">
                      <Globe className="h-3.5 w-3.5" />
                      {source.name}
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            {selectedSource && (
              <span className="text-xs text-muted-foreground">
                {selectedSource.document_count} docs
              </span>
            )}
          </div>
        </div>

        {/* MESSAGES */}
        <div className="flex-1 overflow-y-auto px-8 py-6 space-y-6 bg-muted/10">
          {messages.length === 0 ? (
            <div className="flex h-full flex-col items-center justify-center text-center">
              <Bot className="mb-4 h-10 w-10 text-muted-foreground/40" />
              <p className="text-sm text-muted-foreground">
                Ask something about your indexed data.
              </p>
            </div>
          ) : (
            messages.map((msg, i) => (
              <div
                key={i}
                className={cn(
                  "flex gap-3",
                  msg.role === "user" ? "justify-end" : "justify-start"
                )}
              >
                {msg.role === "assistant" && (
                  <div className="h-8 w-8 rounded-lg bg-primary/10 flex items-center justify-center">
                    <Bot className="h-4 w-4 text-primary" />
                  </div>
                )}

                <div
                  className={cn(
                    "max-w-[65%] rounded-2xl px-5 py-3 text-sm leading-relaxed shadow-sm",
                    msg.role === "user"
                      ? "bg-primary text-primary-foreground"
                      : "bg-background border"
                  )}
                >
                  {msg.content}
                </div>

                {msg.role === "user" && (
                  <div className="h-8 w-8 rounded-lg bg-secondary flex items-center justify-center">
                    <User className="h-4 w-4 text-muted-foreground" />
                  </div>
                )}
              </div>
            ))
          )}

          {sending && (
            <div className="flex gap-3">
              <div className="h-8 w-8 rounded-lg bg-primary/10 flex items-center justify-center">
                <Bot className="h-4 w-4 text-primary" />
              </div>
              <div className="rounded-2xl border px-5 py-3 text-sm">
                Thinking...
              </div>
            </div>
          )}

          <div ref={messagesEndRef} />
        </div>

        {/* INPUT */}
        <div className="border-t px-6 py-4 bg-background">
          <form
            onSubmit={(e) => {
              e.preventDefault()
              sendMessage()
            }}
            className="flex gap-3"
          >
            <Input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              placeholder="Ask a question..."
              disabled={sending}
              className="flex-1 rounded-xl"
            />
            <Button type="submit" disabled={!input.trim() || sending}>
              <Send className="h-4 w-4" />
            </Button>
          </form>
        </div>
      </div>
    </div>
  )
}

export default function ChatPage() {
  return (
    <Suspense fallback={<div className="flex h-[calc(100vh-140px)] items-center justify-center">Loading...</div>}>
      <ChatPageContent />
    </Suspense>
  )
}
